.offer {
  min-height: 148px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  border-right: 1px solid rgba(24, 35, 83, 0.1);
  padding-top: 12px;

  @include desktop {
    padding-top: 15px;
  }

  @include tablet {
    position: relative;
    min-height: auto;
    flex-direction: row;
    flex-wrap: wrap;
    padding-top: 25px;
    padding-left: 23px;
    padding-right: 12px;
    padding-bottom: 21px;
    border-right: 0;
    border-bottom: 1px solid rgba(24, 35, 83, 0.1);
  }
}

.offer br {
  display: none;
}

.offer img {
  margin-bottom: 30px;

  @include desktop {
    margin-bottom: 34px;
  }

  @include tablet {
    width: 20px;
    margin-bottom: 0;
    margin-right: 27px;
  }
}

.offer__description {
  width: 123px;
  margin-bottom: 7px;

  @include tablet {
    width: 200px;
    margin-bottom: 0;
  }
}

.offer__description p {
  color: #182353;
  font-size: 15px;
  letter-spacing: -0.52px;
  line-height: 16px;
  text-transform: uppercase;
  font-weight: 700;
  font-style: italic;
  text-align: center;
  padding: 0;
  margin: 0;

  @include desktop {
    font-size: 13px;
    line-height: 13px;
  }

  @include tablet {
    text-align: left;
    font-size: 13px;
    line-height: 13px;
  }
}

.offer__question {
  position: relative;
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: center;

  @include tablet {
    width: auto;
    margin-left: auto;
    position: static;
  }
}

.offer__button {
  @include button-reset;

  width: 18px;
  height: 18px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.offer__button img {
  margin: 0;
}

.offer__show {
  position: absolute;
  top: 50px;
  left: 72px;
  display: none;
  background-color: rgba(255, 255, 255, 0.98);
  box-shadow: 0 2px 40px 0 rgba(24, 35, 83, 0.15);
  width: 205px;
  border-radius: 5px;

  &::after {
    content: "";
    display: block;
    width: 16px;
    height: 16px;
    position: absolute;
    top: -2px;
    left: 20px;
    transform: rotate(-45deg);
    background-color: rgba(255, 255, 255, 0.98);
  }
}

.offer__button:hover + .offer__show,
.offer__button:focus + .offer__show {
  display: block;

  @include tablet {
    top: auto;
    left: 25px;
    bottom: -100%;
    z-index: 2;
  }
}

.offer__show p {
  width: 100%;
  color: #182353;
  font-size: 16px;
  line-height: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: left;
  padding-left: 19px;
  padding-top: 11px;
  padding-bottom: 10px;
  padding-right: 5px;
  margin: 0;
}
