.product {
  @include link-reset;

  position: relative;
  box-sizing: border-box;
  width: 274px;
  height: auto;
  min-height: 340px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid rgba(106, 121, 135, 0.3);
  padding: 18px 20px 9px;

  @include desktop {
    width: 100%;
    padding-bottom: 41px;
  }

  @include tablet {
    padding: 18px 7px 9px 20px;
    min-height: 329px;
  }
}

.product__img {
  height: 220px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 24px;

  @include desktop {
    margin-bottom: 29px;
  }

  @include tablet {
    height: 180px;
    align-items: flex-start;
    margin-bottom: 18px;
  }
}

.product__img img {
  @include tablet {
    max-width: 88%;
  }
}

.product__info {
  width: 100%;
  position: relative;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 0 -30px 25px 0 rgba(255, 255, 255, 0.9);
  transition: transform $transition;

  @include desktop {
    margin-bottom: 14px;
  }

  @include tablet {
    margin-bottom: 15px;
  }
}

.product:hover .product__info {
  transform: translateY(-59px);
}

.product__info h3 {
  @include title-reset;

  color: #182353;
  font-size: 20px;
  letter-spacing: -0.8px;
  line-height: 22px;
  text-transform: uppercase;
  font-weight: 700;
  text-align: center;
  font-style: italic;
  padding: 0;
  margin: 0 0 10px 0;

  @include tablet {
    font-size: 15px;
    line-height: 17px;
    margin-bottom: 3px;
  }
}

.product__info p {
  width: 100%;
  color: #182353;
  font-weight: 300;
  line-height: 20px;
  font-size: 16px;
  text-align: center;
  font-style: italic;
  padding: 0;
  margin: 0;
}

.product__btn {
  display: block;
  position: absolute;
  left: 50%;
  bottom: 29px;
  transform: translateX(-50%);
  opacity: 0;
  padding: 0 20px;
  background-color: #242be5;
  width: 97px;
  text-align: center;
  font-size: 15px;
  line-height: 35px;
  letter-spacing: -0.6px;
  text-transform: uppercase;
  color: #ffffff;
  font-weight: 700;
  transition: opacity $transition;

  @include desktop {
    position: static;
    opacity: 1;
    width: 97px;
    transform: translateX(0);
  }

  @include tablet {
    width: 91px;
    transform: translateX(-6px);
    font-size: 13px;
    line-height: 31px;
    margin: 0 auto;
    transition: none;
  }
}

.product:hover .product__btn {
  opacity: 1;
}
