// СБРОС КАСТОМНЫХ СТИЛЕЙ
@mixin title-reset {
  font-weight: normal;
  margin: 0;
  padding: 0;
}

@mixin list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin link-reset {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  margin: 0;
  padding: 0;
}

@mixin button-reset {
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  color: inherit;
}

// меньше 1024
@mixin desktop {
  @media (max-width: $width-desktop - 1px) {
    @content;
  }
}

// меньше 768
@mixin tablet {
  @media (max-width: $width-tablet - 1px) {
    @content;
  }
}

// меньше 1024 и переворот экрана
@mixin mobileLand {
  @media (max-width: $width-desktop - 1px) and (orientation: landscape) {
    @content;
  }
}

// РЕТИНА
@mixin retina {
  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    @content;
  }
}
