.recommendations {
  width: 100%;
  padding-top: 15px;
  overflow: hidden;
  margin-bottom: 74px;

  @include desktop {
    margin-bottom: 50px;
  }

  @include tablet {
    margin-bottom: 37px;
  }
}

.recommendations__title {
  width: 100%;
  margin-bottom: 30px;

  @include desktop {
    margin-bottom: 15px;
  }

  @include tablet {
    margin-bottom: 15px;
  }
}

.recommendations__title h2 {
  @include title-reset;

  display: block;
  width: 100%;
  color: #182353;
  font-size: 30px;
  letter-spacing: -1px;
  line-height: 30px;
  text-transform: uppercase;
  text-align: center;
  font-weight: 700;
  font-style: italic;

  @include tablet {
    font-size: 18px;
    line-height: 18px;
  }
}

.recommendations__slider {
  position: relative;
  width: 100%;
  padding-left: 40px;
  padding-right: 40px;
}

.recommendations__btn {
  @include button-reset;

  display: block;
  width: 30px;
  height: 100px;
  background-color: $experts-bg;
  background-repeat: no-repeat;
  background-size: 6px 10px;
  background-position: center;
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-47%);

  &--left {
    left: 0;
    background-image: url("../img/arr_prev.png");
  }

  &--right {
    right: 0;
    background-image: url("../img/arr_next.png");
  }

  @include desktop {
    transform: translateY(-79%);
  }

  @include tablet {
    height: 80px;
    transform: translateY(-50%);
  }
}
