.technologies {
  padding: 70px 50px 0 50px;

  @include desktop {
    padding: 53px 37px 0 37px;
  }

  @include tablet {
    padding-top: 38px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

.technologies__rows {
  margin-bottom: 100px;
  display: flex;
  justify-content: space-between;

  @include tablet {
    flex-direction: column;
    justify-content: flex-start;
    margin-bottom: 13px;

    &--first {
      flex-direction: column-reverse;
    }

    &--title {
      padding-top: 58px;
      position: relative;
    }
  }
}

.technologies__block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 46.66%;

  @include desktop {
    width: 48%;
  }

  @include tablet {
    width: 100%;
    margin-bottom: 30px;
  }
}

.technologies__block h2 {
  color: #182353;
  font-weight: 700;
  font-size: 30px;
  letter-spacing: -1px;
  line-height: 30px;
  text-transform: uppercase;
  font-style: italic;
  padding: 0;
  margin: 0 0 60px 0;

  @include desktop {
    margin-bottom: 42px;
  }

  @include tablet {
    font-size: 18px;
    line-height: 18px;
  }
}

.technologies__rows--title h2 {
  @include tablet {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
  }
}

.technologies__block h3 {
  color: #182353;
  font-weight: 700;
  font-size: 20px;
  letter-spacing: -0.8px;
  line-height: 22px;
  font-style: italic;
  text-transform: uppercase;
  padding: 0;
  margin: 0 0 15px 0;

  @include tablet {
    font-size: 15px;
    line-height: 17px;
    margin-bottom: 10px;
  }
}

.technologies__block p {
  display: block;
  opacity: 0.8;
  color: #182353;
  font-size: 20px;
  font-weight: 300;
  line-height: 25px;
  font-style: italic;
  width: 85%;
  padding: 0;
  margin: 0;

  @include desktop {
    width: 100%;
  }

  @include tablet {
    font-size: 15px;
    line-height: 17px;
  }
}
