.news {
  position: relative;
  background-color: $slider-bg;
  padding: 71px 0 60px 0;
  overflow: hidden;
  min-height: 650px;

  @include desktop {
    padding-top: 55px;
    padding-bottom: 39px;
  }

  @include tablet {
    min-height: auto;
    background-image: url("../img/pattern_dark.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: 40px;
    padding-bottom: 17px;
  }
}

.news__top-container {
  position: relative;
  width: 100%;
  padding-left: 5%;
  padding-right: 5%;
  padding-bottom: 126px;
  margin-bottom: 40px;

  @include desktop {
    margin-bottom: 29px;
  }

  @include tablet {
    padding-bottom: 85px;
    margin-bottom: 14px;
  }
}

.news h2 {
  display: block;
  width: 100%;
  text-align: center;
  font-weight: 700;
  color: #ffffff;
  font-size: 30px;
  letter-spacing: -1px;
  line-height: 30px;
  font-style: italic;
  text-transform: uppercase;
  padding: 0;
  margin: 0;

  @include tablet {
    width: 209px;
    font-size: 18px;
    line-height: 18px;
    margin-left: auto;
    margin-right: auto;
  }
}

.news__btn {
  @include button-reset;

  position: absolute;
  top: 43%;
  display: block;
  width: 40px;
  height: 80px;
  background-size: 6px 10px;
  background-repeat: no-repeat;
  background-position: center;
  background-color: $slider-light-bg;
  opacity: 1;
  transform: translateY(-50%);
  transition: opacity $transition, background $transition;

  &--next {
    background-image: url("../img/arr_next.png");
    right: 0;
  }

  &--prev {
    background-image: url("../img/arr_prev.png");
    left: 0;
  }

  &:hover,
  &:focus {
    background-size: 8px 12px;
  }

  &:active {
    opacity: 0.5;
  }

  @include desktop {
    display: none;
  }
}

.news__btn.swiper-button-disabled {
  display: none;
}

.news .swiper-wrapper {
  padding-bottom: 10px;
}

.news__slider-controls {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  max-width: 990px;
  padding-left: 5%;
  padding-right: 5%;

  @include tablet {
    padding-left: 7%;
  }
}

.news__toggle {
  @include button-reset;

  position: relative;
  width: 170px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: $slider-light-bg;
  box-sizing: border-box;
  border: 6px solid transparent;
  transition: border $transition;

  &:hover {
    border-color: #ffffff;
  }

  &--active {
    border-color: $yellow;

    &:hover {
      border-color: $yellow;
    }

    &::before {
      display: block;
      content: "";
      position: absolute;
      bottom: -12px;
      left: 50%;
      width: 14px;
      height: 15px;
      transform: translateX(-50%) rotate(45deg);
      background-color: $slider-light-bg;
    }

    &::after {
      display: block;
      content: "";
      position: absolute;
      bottom: -12px;
      left: 50%;
      width: 15px;
      height: 15px;
      border-right: 6px solid #fdb813;
      border-bottom: 6px solid #fdb813;
      transform: translateX(-50%) rotate(45deg);
    }
  }

  @include desktop {
    width: 150px;
  }

  @include tablet {
    width: 101px;
    height: 54px;
  }
}

.news__content {
  padding-left: 5%;
  padding-right: 5%;

  @include tablet {
    padding-left: 20px;
    padding-right: 25px;
  }
}

.news__toggle img {
  max-width: 50%;
}
