*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  font-family: "Proxima", "Arial", sans-serif;
  font-size: 18px;
  line-height: 20px;
  margin: 0;
  padding: 0;
  background-color: $body-background;
  min-width: $width-mobile;
  width: 100%;
  height: 100%;
  color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
}

input[type="email"],
input[type="password"],
input[type="text"],
input[type="tel"],
textarea {
  appearance: none;
  outline: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  border: 0;
  margin: -1px;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
