.news-slide {
  display: none;
  justify-content: space-between;
  background-image: url("../img/pattern_dark.jpg");
  background-repeat: no-repeat;
  background-size: cover;

  &--active {
    display: flex;
  }

  @include tablet {
    flex-direction: column;
    justify-content: flex-start;
    background: none;
  }
}

.news-slide__img {
  position: relative;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;

  @include tablet {
    width: 100%;
    justify-content: flex-start;
    margin: 0;
  }
}

.news-slide__img--fix img {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: -40px;

  @include tablet {
    position: static;
  }
}

.news-slide__img img {
  max-width: 83%;

  @include tablet {
    width: 87%;
    margin: 0 0 0 -19px;
  }
}

.news-slide__info {
  width: 50%;
  padding: 50px 40px 40px 30px;

  @include desktop {
    padding: 50px 38px 40px 19px;
  }

  @include tablet {
    width: 100%;
    padding-top: 33px;
    padding-left: 0;
    padding-right: 0;
    padding-bottom: 0;
  }
}

.news-slide__info h3 {
  font-size: 20px;
  letter-spacing: -0.8px;
  line-height: 22px;
  text-transform: uppercase;
  font-weight: 700;
  font-style: italic;
  padding: 0;
  margin: 0 0 20px 0;

  @include tablet {
    font-size: 15px;
    line-height: 17px;
  }
}

.news-slide__info p {
  font-size: 18px;
  font-weight: 300;
  line-height: 20px;
  font-style: italic;
  padding: 0;
  margin: 0;

  @include tablet {
    font-size: 15px;
    line-height: 17px;
  }
}
