.experts {
  display: flex;
  justify-content: space-between;
  background-image: url("../img/experts/experts_bg@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: $experts-bg;
  padding: 70px 5% 69px 5%;
  margin-bottom: 57px;

  @include desktop {
    padding-top: 51px;
    padding-bottom: 53px;
    margin-bottom: 36px;
  }

  @include tablet {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-top: 33px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 56px;
    margin-bottom: 28px;
  }
}

.experts__title {
  width: 200px;
  margin-bottom: 30px;

  @include tablet {
    margin-bottom: 19px;
  }
}

.experts__title h2 {
  @include title-reset;

  font-size: 30px;
  letter-spacing: -1px;
  line-height: 30px;
  text-transform: uppercase;
  font-weight: 700;
  font-style: italic;
  color: #ffffff;
  padding-top: 7px;

  @include tablet {
    font-size: 18px;
    line-height: 18px;
  }
}

.experts__column {
  width: 49%;

  @include tablet {
    width: 100%;
    margin-bottom: 17px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.experts__athlete {
  padding: 31px 31px 13px 29px;
  background-color: $experts-bg;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  @include desktop {
    flex-direction: column;
    padding-top: 28px;
  }

  @include tablet {
    padding-top: 18px;
    padding-left: 20px;
    padding-right: 26px;
    padding-bottom: 6px;
  }
}

.experts__athlete br {
  @include desktop {
    display: none;
  }
}

.experts__athlete-name {
  width: 125px;
  margin-right: 10px;

  @include desktop {
    width: 100%;
    display: flex;
    margin-right: 0;
    margin-bottom: 17px;
  }
}

.experts__athlete-name img {
  margin-bottom: 13px;

  @include desktop {
    width: 61px;
    height: 61px;
    margin-bottom: 0;
    margin-right: 19px;
  }

  @include tablet {
    width: 48px;
    height: 48px;
    margin-right: 15px;
  }
}

.experts__athlete-name h3 {
  font-size: 20px;
  letter-spacing: -0.8px;
  line-height: 22px;
  text-transform: uppercase;
  font-weight: 700;
  font-style: italic;
  padding: 0;
  margin: 0;

  @include desktop {
    display: block;
    width: 160px;
    padding-top: 8px;
  }

  @include tablet {
    width: 140px;
    font-size: 18px;
    line-height: 18px;
  }
}

.experts__athlete-info {
  width: 220px;

  @include desktop {
    width: 100%;
  }
}

.experts__athlete-name p {
  display: block;
  width: 100%;
  font-size: 18px;
  font-weight: 300;
  line-height: 20px;
  font-style: italic;
  padding: 0;
  margin: 0;
}

.experts__athlete-info p {
  font-size: 18px;
  font-weight: 300;
  line-height: 20px;
  font-style: italic;
  padding: 0;
  margin: 0 0 20px 0;

  @include tablet {
    font-size: 13px;
    line-height: 15px;
    margin-bottom: 15px;
  }
}
