.equipment-center {
  width: 100%;
  padding-bottom: 70px;

  @include desktop {
    padding-bottom: 47px;
  }

  @include tablet {
    padding: 0;
    margin-bottom: 17px;
  }
}

.equipment-center__banner {
  position: relative;
  width: 100%;
  min-height: 520px;
  overflow: hidden;
  margin-bottom: 64px;
  z-index: 2;

  @include desktop {
    margin-bottom: 49px;
  }

  @include tablet {
    min-height: 585px;
    margin-bottom: 15px;
  }
}

.equipment-center__background {
  position: absolute;
  width: $max-width;
  height: 100%;
  top: 0;
  right: 0;
  z-index: 0;
  padding-bottom: 150px;

  @include desktop {
    background-image: url("../img/banner_bg@2x.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 129px center;
  }

  @include tablet {
    width: 100%;
    background-image: url("../img/banner_bg_mobile.png");
    background-position: right;
  }
}

.equipment-center__background img {
  @include desktop {
    display: none;
  }
}

.equipment-center__info {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-left: 50px;
  padding-right: 50px;
  padding-top: 45px;
  z-index: 1;

  @include desktop {
    padding-left: 38px;
    padding-right: 38px;
  }

  @include tablet {
    padding-top: 24px;
    padding-left: 19px;
    padding-right: 10px;
  }
}

.equipment-center__text {
  width: 380px;
  margin-bottom: 15px;

  &--adress {
    width: 100%;
    display: flex;
    align-items: center;
    margin-bottom: 113px;
  }

  @include tablet {
    width: 265px;

    &--adress {
      margin-bottom: 25px;
    }
  }
}

.equipment-center__text img {
  margin-right: 8px;

  @include tablet {
    margin-right: 5px;
    width: 9px;
  }
}

.equipment-center__text p {
  font-size: 20px;
  font-weight: 300;
  line-height: 25px;
  font-style: italic;
  color: #ffffff;
  padding: 0;
  margin: 0;

  @include tablet {
    font-size: 15px;
    line-height: 17px;
  }
}

.equipment-center__text--adress p {
  font-size: 16px;
  line-height: 20px;

  @include tablet {
    font-size: 13px;
  }
}

.equipment-center h1 {
  @include title-reset;

  font-size: 45px;
  letter-spacing: -1.5px;
  line-height: 38px;
  text-transform: uppercase;
  font-weight: 700;
  font-style: italic;
  width: 420px;
  margin-bottom: 15px;

  @include tablet {
    width: 270px;
    font-size: 25px;
    line-height: 25px;
    margin-bottom: 12px;
  }
}

.equipment-center__manufacturer-slider {
  position: absolute;
  width: 100%;
  max-width: 890px;
  min-height: 100px;
  bottom: 50px;
  left: 50px;
  background-color: rgba(244, 244, 245, 0.2);
  border-left: 5px solid #242be5;
  border-right: 5px solid #242be5;
  padding-left: 40px;
  padding-right: 38px;
  z-index: 2;

  @include desktop {
    left: 40px;
    padding-left: 33px;
  }

  @include tablet {
    min-height: 80px;
    left: 20px;
    bottom: 24px;
    border-width: 3px;
    padding-left: 26px;
  }
}

.equipment-center__manufacturer {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: auto;
  min-height: 100px;
  margin-right: auto;

  &:last-child {
    margin-right: 0;
  }

  @include tablet {
    min-height: 80px;
    padding-bottom: 5px;
  }
}

.equipment-center__manufacturer img {
  width: 80%;
}

.equipment-center__manufacturer-slider .swiper-wrapper {
  box-sizing: border-box;
  width: 803px;

  @include tablet {
    width: 666px;
  }
}

.equipment-center__offers {
  @include list-reset;

  display: flex;
  justify-content: space-between;

  @include tablet {
    flex-direction: column;
    justify-content: flex-start;
  }
}

.equipment-center__offer {
  width: 20%;

  @include tablet {
    width: 100%;
  }
}
